/* Font Styles */
html,
body {
  width: 100%;
  height: 100%;
}

body {
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 14px;
  color: #2f2f2f;
}

body > * {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  padding: 0 15px;
  text-transform: uppercase;
}

.gf-container {
  width: 100%;
  margin: 0 auto;
  max-width: 1200px;
}

.gf-container--fluid {
  max-width: 96%;
}

.gf-row {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-bottom: 30px;
  align-items: flex-start;
}

.gf-col {
  padding: 0 15px;
  box-sizing: border-box;
  transition: width cubic-bezier(0.47, 0, 0.745, 0.715) 400ms;
}

.gf-col-auto {
  flex: auto;
}

.gf-col--vspace {
  margin-bottom: 30px;
}

.gf-col-1 {
  width: calc(100%/16 * 1);
}

.gf-col-2 {
  width: calc(100%/16 * 2);
}

.gf-col-3 {
  width: calc(100%/16 * 3);
}

.gf-col-4 {
  width: calc(100%/16 * 4);
}

.gf-col-5 {
  width: calc(100%/16 * 5);
}

.gf-col-6 {
  width: calc(100%/16 * 6);
}

.gf-col-7 {
  width: calc(100%/16 * 7);
}

.gf-col-8 {
  width: calc(100%/16 * 8);
}

.gf-col-9 {
  width: calc(100%/16 * 9);
}

.gf-col-10 {
  width: calc(100%/16 * 10);
}

.gf-col-11 {
  width: calc(100%/16 * 11);
}

.gf-col-12 {
  width: calc(100%/16 * 12);
}

.gf-col-13 {
  width: calc(100%/16 * 13);
}

.gf-col-14 {
  width: calc(100%/16 * 14);
}

.gf-col-15 {
  width: calc(100%/16 * 15);
}

.gf-col-16 {
  width: calc(100%/16 * 16);
}

.gf-col-flex-1 {
  flex: 1;
}

.gf-col-flex-2 {
  flex: 2;
}

.gf-col-flex-3 {
  flex: 3;
}

.gf-col-flex-4 {
  flex: 4;
}

.gf-col-flex-5 {
  flex: 5;
}

.gf-col-flex-6 {
  flex: 6;
}

.gf-col-flex-7 {
  flex: 7;
}

.gf-col-flex-8 {
  flex: 8;
}

@media (max-width: 800px) {
  .gf-container--fluid {
    width: 100%;
  }
  .gf-row {
    flex-direction: column;
  }
  .gf-col {
    width: 100%;
    margin-bottom: 30px;
  }
  .gf-col:last-of-type {
    margin-bottom: 0;
  }
}

.gf-col--display div {
  border: 4px solid #2f2f2f;
  text-align: center;
  padding: 12px;
  border-radius: 2px;
  font-weight: 700;
}

.gf-button {
  background: #efefef;
  text-align: center;
  align-items: center;
  justify-content: center;
  display: inline-flex;
  border-radius: 200em;
  text-transform: uppercase;
  cursor: pointer;
  box-sizing: border-box;
  border: none;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 700;
  padding: 1em 3em;
  border: none;
  position: relative;
  margin-right: 5px;
  letter-spacing: 1px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.gf-button:focus {
  outline: 0;
}

.gf-button:hover {
